@include media-breakpoint-down(lg) {
    html {
        max-width: 540px;
        margin: 0 auto;
    }

    .container {
        width: 100%;
        padding: 0 16px;
        margin-right: auto;
        margin-left: auto;
    }
}

.container {
    width: var(--container);
    min-width: var(--container);
    margin-right: auto;
    margin-left: auto;
}

.site-body {
    .container {
        > * {
            margin-bottom: $gutter-zone-v;

            &:last-child {
                margin-bottom: 0;
            }
        }
    }
}

.landing-page {
    .site-body {
        padding-top: 10px;
        padding-bottom: 0px;

        .container {
            > * {
                margin-bottom: var(--gutter-body);

                &:last-child {
                    margin-bottom: 0;
                }
            }
        }
    }
}

