.story {
    &::after {
        display: block;
        clear: both;
        content: "";
    }

    &__thumb {
        overflow: hidden;
        position: relative;

        @include media-breakpoint-down(md) {
            height: inherit !important;
        }

        // -- không sửa
        a {
            width: 100%;
            display: flex;
            position: relative;
        }

        img {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            object-fit: cover;
            text-indent: -9999px;
            transition: transform 0.5s ease-out 0.1s;
        }

        // -- kết thúc
    }

    &__heading {
        display: flex;
        font-size: 36px;

        a {
            display: block;
            transition: 0.5s;
            color: var(--primary, #D8A298);
            font-family: var(--font-family-sans-serif);
            line-height: 122%;
            font-weight: $font-weight-semibold;

            &:hover {
                color: var(--secondary, #5f423d) !important;
            }

            [class^="ic-"] {
                float: left;
                margin-right: 8px;
                margin-top: 4px;
            }
        }
    }

    &__summary {
        display: flex;
        font-weight: $font-weight-normal;
        color: var(--summary);
        font-size: 16px;

        > * {
            display: block;
            overflow: hidden;
            margin-bottom: 0;
            font-weight: $font-weight-normal;
            color: var(--summary);
        }
    }

    .time {
        color: var(--gray-600, #6c757d);
        font-size: 12px;
        display: inline-block;
        line-height: 150%;
        font-weight: $font-weight-normal;
    }

    .title-cate {
        color: var(--primary, #980700);
        font-weight: $font-weight-semibold;
        line-height: 150%;
        text-transform: uppercase;
    }

    &__relate {
        > * {
            display: block;
        }
    }

    &__sub-heading a {
        &:hover {
            color: var(--primary, #980700) !important;
        }
    }

    @include no-photo;
}