// ----------------------------------
// INFO : dev style
// ----------------------------------
.hidden {
    display: none !important;
}

// ----------------------------------
// INFO : style global
// ----------------------------------
.landing-page {
    a {
        transition: 0.3s;
        color: var(--primary, #D8A298);

        &:hover {
            color: var(--secondary, #5f423d) !important;
        }
    }

    .site-body {
        background: #000E28 url(../img/landing-bg.jpg) center top no-repeat;

        @media only screen and (min-width: 1920px) {
            background: #000E28 url(../img/landing-bg.jpg) center top repeat-x;
        }
    }

    .box-heading {
        .title {
            transition: all 0.3s;
            display: block;
            color: var(--primary, #D8A298);
        }
    }
}

.site-body.resigter-bg {
    background-image: url(../img/resigter-banner.jpg), url(../img/resigter-bg.jpg);
    background-position: center top, center top;
    background-repeat: no-repeat, no-repeat;

    @include media-breakpoint-down(lg) {
        background-size: contain, contain;
        background-repeat: no-repeat, repeat-y;
        background-position: center top 50px, center top;
    }
}

.logo {
    width: 150px;
    height: 50px;
    background: url(../img/logo.png) center center / auto 50px no-repeat;
    margin: 15px auto 30px !important;
    text-indent: -99999em;

    @include media-breakpoint-up(xl) {
        margin: 35px auto 30px !important;
        width: 279px;
        height: 101px;
        background: url(../img/logo.png) center center / 279px 101px no-repeat;
        margin: 55px auto 45px;
    }
}

.video-player {
    width: 100%;

    video {
        width: 100%;
        height: auto;
    }
}

.event-time {
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 8px;

    span {
        text-align: center;
    }

    span:nth-child(1) {
        color: #D8A298;
        font-size: 40px;
        line-height: 90%;
    }

    span:nth-child(2) {
        color: #EAEAEA;
        font-size: 40px;
        line-height: 90%;
    }

    span:nth-child(3) {
        color: #D8A298;
        font-size: 16px;
        line-height: 122%;
    }

    @include media-breakpoint-up(xl) {
        gap: 20px;

        span:nth-child(1) {
            font-size: 96px;
        }

        span:nth-child(2) {
            font-size: 96px;
        }

        span:nth-child(3) {
            font-size: 32px;
        }
    }
}

.timestamp-txt {
    display: grid;
    gap: 80px;

    @include media-breakpoint-up(xxl) {
        gap: 130px;
        grid-template-columns: repeat(2, 1fr);
    }

    .timestamp {
        display: flex;
        gap: 10px;
        justify-content: center;

        @include media-breakpoint-up(xxl) {
            justify-content: flex-end;
        }

        span {
            background: url(../img/bg-item.png) center center no-repeat;
            display: block;
            width: 64px;
            height: 50px;
            font-size: 46px;
            color: #F2F2F2;
            text-align: center;
            font-family: var(--font-family-second);
            position: relative;

            @include media-breakpoint-up(xl) {
                width: 128px;
                height: 100px;
                font-size: 98px;
            }

            &:before {
                font-family: $font-family-sans-serif;
                color: #F2F2F2;
                text-align: center;
                position: absolute;
                text-transform: uppercase;
                font-size: 16px;
                left: 17px;
                bottom: -30px;

                @include media-breakpoint-up(xl) {
                    font-size: 18px;
                    left: 45px;
                    bottom: -35px;
                }
            }
        }

        span:nth-child(1) {
            &:before {
                content: "Ngày";
            }
        }

        span:nth-child(2) {
            &:before {
                content: "Giờ";
            }
        }

        span:nth-child(3) {
            &:before {
                content: "Phút";
            }
        }

        span:nth-child(4) {
            &:before {
                content: "Giây";
            }
        }
    }

    .txt {
        display: grid;

        span:nth-child(1) {
            color: #D8A298;
            text-align: center;
            font-size: 32px;
            font-weight: 700;
            line-height: 122%;
            margin-bottom: 20px;

            @include media-breakpoint-up(xl) {
                font-size: 48px;
            }
        }

        span:nth-child(2),
        span:nth-child(3) {
            color: #FFF;
            text-align: center;
            font-size: 20px;
            font-weight: 400;
            line-height: 143.738%;

            @include media-breakpoint-up(xl) {
                font-size: 32px;
            }
        }

        span:nth-child(3) {
            color: #D8A298;
        }
    }
}

.form-txt {
    display: grid;
    gap: 30px;
    justify-content: center;

    @include media-breakpoint-up(xl) {
        gap: 100px;
        grid-template-columns: repeat(2, 1fr);
    }

    .form-resiger {
        display: grid;
        padding: 20px;
        width: 100%;
        gap: 20px;
        border-radius: 8px;
        position: relative;
        overflow: hidden;

        &:before {
            content: "";
            position: absolute;
            width: 100%;
            height: 100%;
            background: linear-gradient(108deg, rgba(255, 255, 255, 0.26) 0%, rgba(255, 255, 255, 0.07) 100%);
            backdrop-filter: blur(10px);
            z-index: 10;
        }

        @include media-breakpoint-up(xxl) {
            padding: 40px 85px 40px;
            width: 660px;
            gap: 40px;
            border-radius: 20px;
        }

        .label {
            color: #EAEAEA;
            font-size: 36px;
            font-weight: 600;
            z-index: 20;
        }

        .wrap-input {
            display: grid;
            gap: 16px;
            z-index: 20;

            input {
                display: flex;
                width: 100%;
                height: 52px;
                padding: 12px 26px;
                font-size: 16px;
                font-weight: 400;
                border-radius: 200px;
                background-color: transparent;
                border-radius: 133px;
                border: 1px solid #C4C4C4;
                color: #fff;

                &::placeholder {
                    color: #fff;
                }
            }
        }

        button {
            display: flex;
            height: 52px;
            padding: 12px 26px;
            justify-content: center;
            align-items: center;
            gap: 10px;
            border-radius: 133px;
            border: 1px solid #17B582;
            background: #D8A298;
            color: #FFF;
            text-align: center;
            font-family: SVN-Gilroy;
            font-size: 16px;
            font-style: normal;
            font-weight: 700;
            line-height: normal;
            z-index: 20;

            @include media-breakpoint-up(xl) {
                width: 485px;
            }
        }
    }

    .txt {
        display: block;
        color: #FFF;
        font-size: 20px;
        line-height: 160%;
        letter-spacing: 0.2px;
        text-align: center;
        width: 100%;
        background: url(../img/logo-text.png) center top / auto 90px no-repeat;
        padding: 110px 30px 0;

        @include media-breakpoint-up(xxl) {
            width: 620px;
            padding: 250px 30px 0;
            background: url(../img/logo-text.png) 0px 0px no-repeat;
        }

        span {
            color: #D8A298;
            font-weight: 700;
            line-height: 145%;
            letter-spacing: 0.2px;
        }
    }
}

.box-news {
    margin-top: 250px;
    position: relative;

    .box-heading {
        position: absolute;
        z-index: 10;
        top: -150px;
        left: 0px;

        .title {
            color: #D8A298;
            font-size: 50px;
            font-weight: 400;
            line-height: 110%;
        }

        @include media-breakpoint-up(xl) {
            .title {
                font-size: 96px;
            }
        }

        @include media-breakpoint-up(xxl) {
            top: -150px;
            left: -110px;
        }
    }

    .box-content {
        display: grid;
        gap: 40px;

        @include media-breakpoint-up(xl) {
            grid-template-columns: repeat(3, 1fr);
            gap: 30px;
        }
    }

    .story {
        .story__thumb {
            margin-bottom: 20px;

            @include media-breakpoint-up(xl) {
                margin-bottom: 50px;
            }

            a {
                padding-bottom: 90%;
            }
        }

        .story__heading {
            margin-bottom: 12px;

            a {
                font-weight: 600;
                font-size: 24px;
                line-height: 122%;
            }

            @include media-breakpoint-up(xl) {
                margin-bottom: 32px;
                padding-right: 80px;

                a {
                    font-size: 36px;
                }
            }
        }

        .story__summary {
            color: #dbdbdb;
            font-size: 16px;
            font-style: normal;
            line-height: 145%;

            @include media-breakpoint-up(xl) {
                padding-right: 80px;
            }
        }
    }
}

.box-lineup {
    position: relative;
    background: url(../img/bg-lineup.png) center top / 200% auto no-repeat;
    padding-top: 200px;

    @include media-breakpoint-up(xl) {
        margin-bottom: 200px !important;
    }

    > .container {
        padding: 0;
    }

    @include media-breakpoint-up(xl) {
        padding-top: 870px;
        margin-top: -200px;
        transform: translateX(-50%);
        width: 100vw;
        left: 50%;
        max-width: calc(100vw - 17px);
        background: url(../img/bg-lineup.png) center top no-repeat;
    }

    .box-heading {
        z-index: 10;

        @include media-breakpoint-down(lg) {
            margin-bottom: 20px !important;
        }

        .title {
            color: #D8A298;
            font-size: 50px;
            font-weight: 400;
            text-transform: uppercase;
            line-height: 112%;

            @include media-breakpoint-up(xl) {
                font-size: 96px;

                span {
                    padding-left: 142px;
                }
            }
        }
    }

    .box-content {
        display: grid;
        gap: 50px;

        @include media-breakpoint-up(xxl) {
            padding-left: 150px;
        }

        .item {
            gap: 10px;
            display: grid;

            @include media-breakpoint-up(xl) {
                gap: 170px;
                grid-template-columns: 210px 1fr;
            }
        }

        .heading {
            color: #D8A298;
            font-size: 36px;
            font-weight: 600;
            line-height: 122%;
            width: 215px;
            min-width: 215px;
            position: relative;

            &:before {
                content: "";
                width: 80px;
                height: 1px;
                background-color: #D8A298;
                position: absolute;
                top: 22px;
                left: 260px;
            }

            &:after {
                content: "";
                width: 7px;
                height: 7px;
                background-color: #D8A298;
                position: absolute;
                top: 19px;
                left: 338px;
                border-radius: 50%;
            }
        }

        .summary {
            color: #FFF;
            font-size: 16px;
            line-height: 145%;
            letter-spacing: 0.16px;
        }
    }
}

.box-chairman {
    position: relative;

    @include media-breakpoint-up(xl) {
        margin-bottom: 150px !important;
    }

    .box-heading {
        z-index: 20;

        @include media-breakpoint-up(xl) {
            position: absolute;
            top: -80px;
            right: 0;
        }

        @include media-breakpoint-up(xxl) {
            top: -50px;
            right: -120px;
        }

        .title {
            color: #D8A298;
            font-size: 50px;
            font-weight: 400;
            line-height: 110%;
            text-transform: uppercase;
            line-height: 112%;

            @include media-breakpoint-up(xl) {
                font-size: 96px;
            }

            @include media-breakpoint-up(xxl) {
                span {
                    padding-left: 142px;
                }
            }
        }
    }

    .box-content {
        display: grid;
        gap: 16px;

        @include media-breakpoint-up(xl) {
            width: 1170px;
            grid-template-columns: repeat(2, 1fr);
            gap: 0px;
        }

        @include media-breakpoint-up(xxl) {
            width: 1366px;
        }

        .big-thumb {
            img {
                width: 100%;
            }
        }

        .big-desc {
            position: relative;
            padding: 20px;

            @include media-breakpoint-up(xl) {
                padding: 0px;
            }

            &:before {
                content: "";
                position: absolute;
                width: 100%;
                height: 100%;
                background: linear-gradient(108deg, rgba(255, 255, 255, 0.26) 0%, rgba(255, 255, 255, 0.07) 100%);
                backdrop-filter: blur(10px);
                z-index: 10;
                top: 0;
                left: 0;
            }

            .heading {
                z-index: 20;
                color: #D8A298;
                font-size: 36px;
                font-weight: 600;
                line-height: 122%;
                position: relative;
                margin-bottom: 30px;

                @include media-breakpoint-up(xl) {
                    margin-top: 150px;
                    margin-left: 70px;
                    margin-bottom: 40px;
                    width: 330px;
                }

                @include media-breakpoint-up(xxl) {
                    margin-top: 208px;
                }

                span {
                    font-weight: 400;
                }
            }

            .txt {
                z-index: 20;
                color: #FFF;
                font-size: 16px;
                line-height: 143.738%;
                letter-spacing: 0.16px;
                position: relative;

                @include media-breakpoint-up(xl) {
                    width: 530px;
                    margin-left: 70px;
                }
            }
        }
    }
}

.box-performance {
    position: relative;

    @include media-breakpoint-up(xl) {
        margin-bottom: 200px !important;
    }

    .box-heading {
        z-index: 10;
        margin-top: 10px;

        .title {
            color: #D8A298;
            font-size: 44px;
            font-weight: 400;
            line-height: 110%;
            text-transform: uppercase;
            line-height: 112%;

            @include media-breakpoint-up(xl) {
                font-size: 96px;

                span {
                    padding-left: 142px;
                }
            }
        }
    }

    .box-content {
        @include media-breakpoint-up(xl) {
            padding-top: 50px;
        }

        .item {
            .thumb {
                margin-bottom: 20px;
                text-align: center;
                position: relative;

                @include media-breakpoint-down(lg) {
                    img:first-child {
                        object-fit: cover;
                        width: 100%;
                    }
                }

                .number {
                    position: absolute;
                    bottom: -45px;
                    right: 0;

                    @include media-breakpoint-up(xl) {
                        right: 10px
                    }

                    @include media-breakpoint-up(xxl) {
                        right: 80px;
                    }
                }

                @include media-breakpoint-up(xl) {
                    margin-bottom: 30px;
                }
            }

            .txt {
                @include media-breakpoint-up(xl) {
                    display: grid;
                    grid-template-columns: 270px 1fr;
                    gap: 110px;
                    padding-right: 200px;
                }

                .title {
                    color: #D8A298;
                    font-size: 36px;
                    line-height: 110%;
                    margin-bottom: 20px;

                    @include media-breakpoint-up(xl) {
                        margin-bottom: 0;
                    }
                }

                .desc {
                    color: #FFF;
                    font-size: 16px;
                    line-height: 143.738%;
                    letter-spacing: 0.16px;
                    width: 100%;

                    @include media-breakpoint-up(xl) {}
                }
            }
        }
    }
}

.box-performance {
    position: relative;

    [class*="swiper-button-"] {
        width: 165px;
        height: 75px;

        @include media-breakpoint-down(lg) {
            transform: scale(.5);
            top: 180px;
        }

        &::after {
            font-size: 0;
        }
    }

    .swiper-button-prev {
        left: calc(50% - 750px);

        @include media-breakpoint-between(lg, xl) {
            left: calc(50% - 565px);
        }

        @include media-breakpoint-down(lg) {
            left: -40px;
        }

        background: url(../img/arrow-left.png) 0px 0px no-repeat;
    }

    .swiper-button-next {
        right: calc(50% - 750px);

        @include media-breakpoint-between(lg, xl) {
            right: calc(50% - 565px);
        }

        @include media-breakpoint-down(lg) {
            right: -40px;
        }

        background: url(../img/arrow-right.png) 0px 0px no-repeat;
    }
}

.box-exp {
    position: relative;
    margin-bottom: 200px !important;

    .box-heading {
        @include media-breakpoint-up(xl) {
            position: absolute;
            left: -60px;
            z-index: 20;
            bottom: -120px;
        }

        .title {
            color: #D8A298;
            font-size: 50px;
            font-weight: 400;
            line-height: 110%;
            text-transform: uppercase;
            line-height: 112%;

            @include media-breakpoint-up(xl) {
                font-size: 96px;

                span {}
            }
        }
    }

    .box-content {
        display: grid;
        gap: 40px;

        @include media-breakpoint-up(xxl) {
            width: 1366px;
            gap: 0px;
        }

        .box-content {
            display: grid;
            gap: 10px;

            @include media-breakpoint-up(xl) {
                gap: 50px;
            }
        }

        .img {
            display: grid;
            width: 100%;
            gap: 16px;

            img {
                width: 100%;
            }

            @include media-breakpoint-up(xl) {
                gap: 16px;
                grid-template-columns: repeat(3, 380px);

                img {
                    width: 380px;
                    height: auto;
                }
            }

            @include media-breakpoint-up(xxl) {
                gap: 31px;
                grid-template-columns: repeat(3, 434px);

                img {
                    width: 434px;
                }
            }
        }

        .desc {
            color: #FFF;
            font-size: 16px;
            line-height: 143.738%;
            letter-spacing: 0.16px;
            margin-left: auto;

            @include media-breakpoint-up(xl) {
                margin-top: 40px;
                width: 690px;
            }
        }
    }
}

.footer {
    height: 180px;
    display: flex;
    text-align: right;
    width: 100%;
    background: url(../img/footer-bg.png) right bottom no-repeat;
    padding-top: 70px;

    @include media-breakpoint-up(xl) {
        background: url(../img/footer-bg.png) center bottom no-repeat;
        padding-top: 110px;
    }

    p {
        color: #FFF;
        font-size: 16px;
        line-height: 122%;

        a {
            color: #fff;
        }
    }

    p:nth-child(1) {
        font-weight: 600;
        margin-bottom: 8px !important;
    }
}

// ======== resigter page
.resigter-banner {
    transform: translateX(-50%);
    width: 100vw;
    left: 50%;
    max-width: calc(100vw - 17px);
    background: url(../img/resigter-banner.jpg) center top no-repeat;
}

.action {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 16px;

    .ic-minus {
        display: block;
        width: 13px;
        height: 13px;
        background: url(../img/ic-minus.png) center center no-repeat;
    }

    .ic-plus {
        display: block;
        width: 13px;
        height: 13px;
        background: url(../img/ic-plus.png) center center no-repeat;
    }

    .ic-plus {}

    button:first-child {
        width: 30px;
        height: 30px;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: #fff;
        border-radius: 8px;
    }

    button:last-child {
        width: 30px;
        height: 30px;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: #fff;
        border-radius: 8px;
    }

    button:hover {
        background-color: #ccc;
    }

    .result {
        color: #FFF;
        font-size: 20px;
        font-weight: 500;
        text-transform: uppercase;
    }
}

.box-resigter {
    padding-top: 180px;

    @include media-breakpoint-up(xl) {
        padding-top: 900px;
    }

    .box-heading {
        z-index: 10;
        margin-top: 10px;
        margin-bottom: 20px;

        .title {
            color: #D8A298;
            font-size: 28px;
            text-transform: uppercase;
            line-height: 130%;
            text-align: center;
            font-weight: 400;

            @include media-breakpoint-up(xl) {
                font-size: 96px;
            }
        }
    }

    .box-content {
        .ticket-wrap {
            display: grid;
            gap: 20px;
            position: relative;
            margin-bottom: 70px;

            @include media-breakpoint-up(xl) {
                grid-template-columns: repeat(2, 1fr);
            }

            .col-50 {
                display: grid;
                gap: 25px;
            }

            .item {
                border-radius: 20px;
                background: #D8A298;
                display: grid;
                position: relative;
                padding: 24px;

                @include media-breakpoint-up(xl) {
                    padding: 28px 44px 24px;
                }

                .action {
                    right: 24px;
                    top: 24px;

                    @include media-breakpoint-up(xl) {
                        top: 28px;
                        right: 38px;
                    }
                }
            }

            .content {
                display: grid;
                gap: 12px;

                span {
                    display: block;
                }
            }

            .title {
                color: #FFF;
                font-weight: 500;
                line-height: 90%;
                font-size: 24px;

                @include media-breakpoint-up(xl) {
                    font-size: 40px;
                }
            }

            .price {
                color: #FFF;
                font-size: 20px;
                font-weight: 400;
                line-height: 90%;
            }

            .action {
                position: absolute;
            }
        }

        .stage-wrap {
            text-align: center;
            display: grid;
            justify-content: center;
            position: relative;
            gap: 20px;
            padding-top: 20px;

            @include media-breakpoint-up(xl) {
                gap: 0px;
                padding-top: 50px;
            }

            &:before {
                content: "";
                position: absolute;
                width: 100%;
                height: 100%;
                background: linear-gradient(108deg, rgba(255, 255, 255, .26) 0, rgba(255, 255, 255, .07) 100%);
                backdrop-filter: blur(10px);
                z-index: 10;
                border-radius: 20px;
            }
        }

        .map {
            img {
                @include media-breakpoint-down(lg) {
                    width: 90%;
                }
            }
        }

        .map,
        .desc {
            z-index: 20;
        }

        .desc {
            text-align: center;
            display: grid;
            color: #FFF;
            font-size: 20px;
            font-weight: 700;
            line-height: 143.738%;
            grid-template-columns: repeat(1, 1fr);
            justify-content: center;
            gap: 20px;
            margin: 24px;

            @include media-breakpoint-up(xl) {
                grid-template-columns: repeat(3, 1fr);
                justify-content: space-between;
                gap: 100px;
                margin: 50px 160px;
            }

            span {
                display: flex;
                justify-content: flex-start;
                align-items: center;
                gap: 24px;

                @include media-breakpoint-up(xl) {
                    width: 200px;
                }
            }

            i {
                display: inline-block;
                width: 32px;
                height: 32px;
                border-radius: 999px;
            }

            .col-33 {
                display: grid;
                gap: 16px;
            }
        }
    }
}

.box-service {
    .box-heading {
        z-index: 10;
        margin-top: 10px;
        margin-bottom: 20px;

        @include media-breakpoint-up(xl) {
            margin-bottom: 40px;
        }

        .title {
            color: #D8A298;
            font-size: 28px;
            text-transform: uppercase;
            line-height: 130%;
            text-align: center;
            font-weight: 400;

            @include media-breakpoint-up(xl) {
                font-size: 96px;
            }
        }
    }

    .box-content {
        display: grid;
        gap: 20px;
        position: relative;
        margin-bottom: 70px;
        justify-content: center;

        .item {
            border-bottom: 1px solid #D8A298;
            display: grid;
            position: relative;
            padding: 12px 24px 28px;

            &.small {
                width: 100%;
                padding: 0;
            }

            &:last-of-type {
                border-bottom: none;
            }

            @include media-breakpoint-up(xl) {
                padding: 48px;
                padding-right: 172px;
                width: 1080px;

                &.small {
                    width: 800px;
                    padding: 0;
                }
            }

            .action {
                right: 24px;
                top: 8px;

                @include media-breakpoint-up(xl) {
                    top: 50px;
                    right: 38px;
                }
            }
        }

        .content {
            display: grid;
            gap: 12px;

            span {
                display: block;
            }
        }

        .title {
            color: #D8A298;
            font-style: normal;
            font-weight: 600;
            line-height: 90%;
            font-size: 28px;

            /* 36px */
            @include media-breakpoint-up(xl) {
                font-size: 40px;
            }
        }

        .price {
            color: #D8A298;
            font-size: 16px;
            font-weight: 400;
            font-style: normal;
            line-height: 90%;

            @include media-breakpoint-up(xl) {
                margin-bottom: 8px;
                font-weight: 700;
                font-size: 24px;
            }
        }

        .desc {
            color: #FFF;
            font-weight: 400;
            line-height: 143.738%;
            letter-spacing: 0.2px;
            font-size: 16px;

            @include media-breakpoint-up(xl) {
                font-size: 20px;
            }

            p {
                font-weight: 700;
                margin: 8px;
            }
        }

        .list {
            display: grid;
            gap: 4px;

            span {
                padding-left: 30px;
                position: relative;

                &:before {
                    content: "";
                    width: 3px;
                    height: 3px;
                    background-color: #fff;
                    position: absolute;
                    left: 12px;
                    top: 12px;
                }
            }
        }

        .action {
            position: absolute;
        }

        > button {
            display: flex;
            height: 52px;
            padding: 12px 26px;
            justify-content: center;
            align-items: center;
            gap: 10px;
            border-radius: 133px;
            border: 1px solid #17B582;
            background: #D8A298;
            margin-left: auto;
            margin-right: auto;
            color: #FFF;
            text-align: center;
            font-size: 16px;
            font-weight: 700;
            width: 100%;

            @include media-breakpoint-up(xl) {
                width: 480px;
            }

            &:hover {
                background: darken($color: #D8A298, $amount: 5);
            }
        }
    }
}

.copy-right {
    color: #D8A298;
    line-height: 150%;
    font-size: 16px;

    @include media-breakpoint-up(xl) {
        font-size: 20px;
    }
}

.back-to-top {
    transition: background-color .3s,
        opacity .5s, visibility .5s;
    opacity: 0;
    visibility: hidden;
    width: 40px;
    height: 40px;
    background: var(--primary, #980700);
    position: fixed;
    bottom: 30px;
    right: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    z-index: 9;

    &:before {
        content: "\e93a";
        font-size: 20px;
        font-family: icomoon !important;
        color: var(--white, #fff);
        font-style: normal;
        font-weight: 400;
        font-variant: normal;
        text-transform: none;
        line-height: 1;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        display: flex;
        align-items: center;
    }

    &.show {
        opacity: 1;
        visibility: visible;
    }
}