// SVN-Gilroy : Bold, SemiBold, Regular, Italic
@font-face {
    font-family: 'SVN-Gilroy';
    src: url('../fonts/gilroy_vh/SVN-GilroyBold.woff2') format('woff2');
    font-weight: 900;
    font-display: swap;
}

@font-face {
    font-family: 'SVN-Gilroy';
    src: url('../fonts/gilroy_vh/SVN-GilroySemiBold.woff2') format('woff2');
    font-style: italic;
    font-weight: 600;
    font-display: swap;
}

@font-face {
    font-family: 'SVN-Gilroy';
    src: url('../fonts/gilroy_vh/SVN-GilroyMedium.woff2') format('woff2');
    font-weight: 500;
    font-display: swap;
}

@font-face {
    font-family: 'SVN-Gilroy';
    src: url('../fonts/gilroy_vh/SVN-Gilroy.woff2') format('woff2');
    font-weight: 400;
    font-display: swap;
}

