// html global
html {
  font-size: 62.5% !important;
}

$body-bg: #F8F9FA;
$body-color: #343A40;
$font-family-sans-serif: "SVN-Gilroy", Arial, Helvetica, sans-serif;
$font-family-serif: "Noto Serif", Arial, Helvetica, sans-serif;
$font-family-second: "share_tech", Arial, Helvetica, sans-serif;
$font-size-base: 1.4rem;
$gutter-body : 30px;
$gutter-zone-v : 30px;
// Fonts-weight
$font-weight-italic: 200;
$font-weight-light: 300;
$font-weight-normal: 400;
$font-weight-medium: 500;
$font-weight-semibold: 600;
$font-weight-bold: 700;
$font-weight-semibolder: 800;
$font-weight-extraBold: 900;
// INFO: Breakpoint for responsive
$m320: 320px;
$m360: 360px;
$m375: 375px;
$m390: 390px;
$m414: 414px;
$sm : 576px;
$md :768px;
$lg :1024px;
$xl: 1280px;
$xxl: 1500px;
$grid-breakpoints: () !default;
$grid-breakpoints: map-merge((
  m320: 320px,
  m360: 360px,
  m375: 375px,
  m390: 390px,
  m414: 414px,
  sm: 576px,
  md: 768px,
  lg: 1024px,
  xl: 1280px,
  xxl: 1600px,
),
$grid-breakpoints);
@import "./mixins/breakpoints";

// 768px
:root {
  // font
  --font-family-sans-serif: "SVN-Gilroy", Arial, Helvetica, sans-serif;
  --font-family-serif: "Noto Serif", Arial, Helvetica, sans-serif;
  --font-family-second: "share_tech", Arial, Helvetica, sans-serif;
  --gutter: 30px;
  --gutter-body: 70px;
  --container: 730px;
  // color
  --primary: #D8A298;
  --secondary: #5f423d;
  --title: #212529;
  --summary: #6C757D;
  --border-color: #E9ECEF;
  --hover: #980700;
  // color default
  --white: #fff;
  --gray-100: #f8f9fa;
  --gray-200: #e9ecef;
  --gray-300: #dee2e6;
  --gray-400: #ced4da;
  --gray-500: #adb5bd;
  --gray-600: #6c757d;
  --gray-700: #495057;
  --gray-800: #343a40;
  --gray-900: #212529;
  --black: #000;
  // orther
  --title-footer: #f8f9fa;
  --333: #333;
}

:root.cn {
  // font
  --font-family-sans-serif: "Georgia", Arial, Helvetica, sans-serif;
  --font-family-serif: "Georgia", Arial, Helvetica, sans-serif;
  --font-family-second: "Georgia", Arial, Helvetica, sans-serif;
}

:root.dark-theme {
  --primary: #fff;
  --secondary: #842029;
  --title: #f8f9fa;
  --summary: #6c757d;
  --border-color: #343a40;
  --hover: #343a40;
  --white: #000;
  --gray-100: #212529;
  --gray-200: #343a40;
  --gray-300: #495057;
  --gray-400: #6c757d;
  --gray-500: #adb5bd;
  --gray-600: #ced4da;
  --gray-700: #dee2e6;
  --gray-800: #e9ecef;
  --gray-900: #f8f9fa;
  --black: #fff;
  --success: #198754;
  --danger: #DC3545;
}

.landing-page {
  :root {
    --primary: #D8A298;
    --secondary: #5f423d;
    --title: #D8A298;
    --summary: #fff;
    --border-color: #C4C4C4;
    --hover: #5f423d;
  }
}

// 0 - 1279px
@include media-breakpoint-down(lg) {
  :root {
    --container: 100%;
    --gutter: 16px;
    --gutter-body: 70px;
  }
}

// 1280px - 1500
@include media-breakpoint-between(xl, xxl) {
  :root {
    --container: 1170px;
  }
}

// 1500px and larger
@include media-breakpoint-up(xxl) {
  :root {
    --container: 1366px;
  }
}